/* Body */

.ContactBody {
    background-color: var(--maincolor);
    padding: 0;
    margin: 0;
    height: 100%;
}

.flexBox {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding-left: 30%;
    padding-right: 30%;
}

.flexTitle {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
}

.square {
    background-color: var(--highlight);
    width: 1rem;
    height: 1rem;
}

.flexBox > h1 {
    text-align:center;
}

.flexRow {
    display: flex;
    justify-content: space-between;
}

.flexCard {
    display: flex;
    background-color: var(--white);
    box-shadow: var(--black) 0px 0.313rem 0.938rem;
    padding: 3rem 0 3rem 3rem;
}

hr {
    width: 50%;
    border: 0.1rem solid var(--highlight);
}

.skillElement {
    display: flex;
    align-items: center;
    gap: 0.2rem;
}

.skillSquare {
    width: 0.7rem;
    height: 0.7rem;
    background-color: var(--highlight);
}


.contactCard {
    background-color: var(--white);
    box-shadow: var(--black) 0px 0.313rem 0.938rem;
    padding: 3rem;
    margin-bottom: 2rem;
    .form {
        display: flex;
        flex-direction: column;
        align-items: center;
        .formFlex {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 100%;
            font-weight: 100;
        
            .formName {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 1rem;
                .formFirstName{
                    width: 50%;
                    input {
                        width: 100%;
                    }
                }
                .formLastName{
                    width: 50%;
                    input {
                        width: 100%;
                    }
                }
            }
        }

    }
}



input, textarea {
    border: 0;
    outline: 0;
    border-bottom: 0.1rem solid var(--black);
    font-size: 1.2rem;
    background-color: var(--white);
    margin-bottom: 2rem;
    font-family: Helvetica, Arial, sans-serif;
    resize:vertical;
    &:hover, &:focus {
        border-bottom: 0.1rem solid var(--highlight);
    } 
}


@media (max-width: 1000px) {
    .ContactBody {
        .flexBox {
            padding: 0 10%;

            .contactCard {
                margin-bottom: 5rem;

                .form {

                    .formFlex {

                        .formName {
                            flex-direction: column;
                            align-items: flex-start;
                            width: 100%;

                            .formFirstName, .formLastName {
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
        }
}